.testimonials {

  &__item {
    margin-bottom: 48px;
    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 28px;
    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    width: 100%;
    grid-column: 2 / 4;
    border-radius: 50%;
    transition: 0.3s;

    &:hover {
      transform: scale(110%);
    }
  }

  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $colorCite;
    margin: 0 0 4px;
  }

  &__role {
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    margin: 0 0 4px;
    color: #9fadc2;
  }
  &__about {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: #3e3e3e;
  }

}

.link {
  text-decoration: none;
  color: $colorCite;
}
